import { NgClass } from '@angular/common';
import { Component, inject, input } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { ClickStopPropagationDirective } from '../../../../shared/directives/click-stop-propagation.directive';
import { TippyDirective } from '../../../../shared/directives/tippy.directive';
import { FieldState } from '../../field-state';

@Component({
    selector: 'app-picker-toggle-text',
    templateUrl: './picker-text-toggle.html',
    styleUrls: ['./picker-text-toggle.scss'],
    imports: [
        MatFormFieldModule,
        MatButtonModule,
        NgClass,
        MatIconModule,
        ClickStopPropagationDirective,
        TippyDirective,
    ]
})
export class PickerTextToggleComponent {
  csDisabled = input<boolean>();
  allowText = input<boolean>();
  isTextMode = input<boolean>(false);

  showBoth = input<boolean>(true);

  pickerData = inject(FieldState).pickerData;

  constructor() { }

  toggleFreeText(disabled: boolean): void {
    if (disabled) return;
    this.pickerData.toggleFreeTextMode();
  }
}
